* {
  font-family: Arial, Helvetica, sans-serif;
}

.container {
  width: 300px;
  background-color: gray;
  margin: auto;
  padding: 30px;
}

.title {
  text-align: center;
  font-family: Arial, Helvetica, sans-serif;
}

input {
  width: 100%;
  border: 1px solid #000;
  border-radius: 3px;
  margin-top: 10px;
  padding: 5px;
  display: block;
}

#price {
  margin-bottom: 10px;
}

.text-right {
  text-align: right;
  margin-bottom: 0;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 30px;
  font-weight: 700;
}

.amount {
  color: #fff;
  background-color: #00f;
  border-radius: 3px;
  margin: 0 5px;
  padding: 1px 5px;
  display: inline-block;
}

